<template>
    <div class="tips" :class="{tipsshow:tips == ''?false:true}">
        {{tips}} <div class="close"></div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                tips:'',
                tiplist:[
                    '',
                    '',
                    '',
                    ''
                ]
            }
        },
        methods:{
            showtipsfn:function(tips){
                this.tips = tips||''
                setTimeout(()=>{
                    this.tips = ''
                    setTimeout(()=>{
                        this.showtipsfn(this.tiplist[Math.floor(Math.random()*4)])
                    },5000)
                },5000)
            }
        },
        mounted(){
            this.showtipsfn()
        }
    }
</script>

<style scoped>
    .tips {
        position: fixed;
        right: 20px;
        bottom: 20px;
        padding: 10px;
        height: 20px;
        min-width: 20px;
        text-align: right;
        background-color: rgb(157, 106, 214);
        border-radius: 10px;
        color: white;
        z-index: 3;
        visibility: hidden;
        opacity: 0;
    }
    .tipsshow{
        visibility: visible !important;
        transition: 700ms;
        opacity: 1 !important;
    }
    .close {
        display: inline;
        margin-left: 5px;
        color: white;
    }
</style>